import React,{Component, Fragment} from 'react';
//import Teamlist from './Teamlist';
import MetaTags from "react-meta-tags";
import LayoutOne from "../layouts/LayoutOne";


class Team extends Component{
	render(){


	const teamlistarray = [
		{
			id:1,
			name:"Adama",
			work:"Web Dev", 
			image:'images/team1.jpg'
		},
		{
			id:1,
			name:"Few",
			work:"De Dev",
			image:'images/team2.jpg'
		},
		{
			id:1,
			name:"Crew",
			work:"Fe Dev",
			image:'images/team3.jpg'
		},
		{
			id:1,
			name:"Deo",
			work:"Dw Dev",
			image:'images/team4.jpg'
		},
	]
 

    const teamlistmap = teamlistarray.map((valu, i) => {
        return(

			<div className="col-lg-3" key={i}>
				<div className="team_wrp">
	                <div className="team_img"> 
	                    <img src={`${valu.image}`} alt="testimonial 1" />
	                </div>
	                <div className="team_info">
	                    <h4>{valu.name}</h4>
	                    <p>{valu.work}</p> 
	                </div>
	        	</div>
			</div> 
        )
    });

	return(
        <Fragment>
        <MetaTags>
          <title>FuodBorne | Team</title>
          <meta
            name="description"
            content="Organic Food React JS Template."
          />
        </MetaTags>
        <LayoutOne>

		<div className="team-page">
			{/*====================  breadcrumb area ====================*/}
			<header id="page-top" className="blog-banner"> 
				<div className="container" id="blog">
					<div className="row blog-header text-center">
						<div className="col-sm-12"> 
							<h3> Our Products </h3>
							<h4><a href="index.html"> Home </a> &gt; Team </h4>
						</div>
					</div> 
				</div> 
			</header>
			{/*====================  End of breadcrumb area  ====================*/} 

			<section className="team_section">	
				<div className="container">	
					<div className="base-header"> 
						<h3>Our Team Member</h3>
					</div>
					<div className="row">
						{teamlistmap}
					</div>
				</div>
			</section>
		</div>

		</LayoutOne>
	</Fragment>

 	)

	}
}

 
export default Team;